<template>
    <div>
      <h1 class="title">Risk Situations</h1>
      <div v-if="loading" class="loader">
        <div></div>
        <div></div>
        <div></div>
      </div>
      <table v-if="!loading && risks.length" class="risk-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Time</th>
            <th>Risk Type</th>
            <th>Location</th>
            <th>Status</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="risk in risks" :key="risk.id">
            <td>{{ risk.id }}</td>
            <td>{{ risk.time }}</td>
            <td>{{ risk.riskType }}</td>
            <td>{{ risk.location }}</td>
            <td>{{ risk.status }}</td>
          </tr>
        </tbody>
      </table>
      <div v-if="!loading && !risks.length" class="no-risk">
        <p>No risk situations found.</p>
      </div>
    </div>
  </template>
  
  <script>
  import axios from 'axios';

  axios.defaults.baseURL = 'http://localhost/data'; // 백엔드 주소

  export default {
    data() {
      return {
        risks: [],
        loading: true
      };
    },
    mounted() {
      axios.get('/data')
        .then(response => {
          this.risks = response.data;
          this.loading = false;
        })
        .catch(error => {
          console.error(error);
          this.loading = false;
        });
    }
  };
  </script>
  
  <style>
  .title {
    text-align: center;
    color: #333;
  }
  
  .loader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
  }
  
  .loader div {
    width: 18px;
    height: 18px;
    margin: 2px;
    background: #3498db;
    border-radius: 50%;
    animation: loader 1.2s infinite ease-in-out both;
  }
  
  @keyframes loader {
    0%, 100% {
      transform: scale(0);
    }
    50% {
      transform: scale(1);
    }
  }
  
  .risk-table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
  }
  
  .risk-table th, .risk-table td {
    padding: 12px;
    border: 1px solid #ddd;
    text-align: left;
  }
  
  .risk-table th {
    background-color: #f2f2f2;
  }
  
  .no-risk {
    text-align: center;
    margin-top: 20px;
    color: #999;
  }
  </style>
